@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Roboto', sans-serif;
}
@font-face {
  font-family: isocteur;
  src: url(/static/media/ISOCTEUR.1d9693c6.ttf);
}
.base-font{
  font-family: isocteur!important;
}
:root{
  background-color: #E5E5E5;
  font-family: 'Roboto', sans-serif;
  color: #383838;
}
body{
  font-family: 'Roboto', sans-serif;
}
#root{
  background-color: #E5E5E5;
}

.navbar{
    padding-left: 50px!important;
    padding-right: 50px!important;
    background: #00140F;
    height: 80px;
}
.logo{
    height: 50px;
    width: 60px;
}
.navbar-light .navbar-brand{
    color:#FFFFFF!important;
    font-size: 24px;
}
.navbar-light .navbar-toggler{
    background:#FFFFFF!important;
}
.navbar-nav li a{
    color: #FFFFFF!important;
    font-size: 24px;
}
.navbar-nav li a:hover{
    color: #ffffffd6!important;
}
.navbar-nav li{
    margin-right: 30px;
}
.hero{
    /* min-height: 85vh; */
    max-width: 100%;
    text-align: center;
    /* background: #00000069; */
}
.hero h1{
    width: 100%;
    /* margin-top: -420px; */
    text-align: center;
    color: #FFFFFF;
    font-size: 80px;
    font-weight: 700;
}
.landing{
    width: 100%;
    height: auto;
    background-color: #053026;
    text-align: center;
    margin-bottom: 50px;
}
.landing img{
    height: 400px;
    margin-top: 45px;
}
.landing h1{
    color: #FFFFFF;
    transform: translateY(-100px);
}
/* swiper */
.swiper-container {
    width: 90%;
    height: 500px;
}
.swiper-slide, .swiper-slide-active{
    width: auto;
}
.swiper-slide {
    width: auto;
    text-align: center;
    background: #E5E5E5;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}
.residential,.commercial,.retail,.corporate,.intitutional,.health,.industry{
    height: 100%;
    position: relative;
    -webkit-filter: brightness(0.6);
            filter: brightness(0.6);
    margin: auto;
}
.residential-head,.commercial-head,.retail-head,.corporate-head,.intitutional-head,.health-head,.industry-head{
    position: absolute;
    color: #fff;
    font-weight: bold;
}

@media screen and (max-width:1024px){
    .navbar-nav li{
        margin-right: 20px;
    }   
    .hero h1{
        margin-top: -300px;
        font-size: 70px;
    }
    .navbar-toggler{
        margin: 0%;
    }
}
@media screen and (max-width:768px){
    .navbar-toggler{
        margin: 0%;
        padding: 5px;
    }
    .hero h1{
        margin-top: -250px;
        font-size: 60px;
    }
    .navbar-collapse{
        z-index: 1;
        background: #00140F;
        text-align: center;
        margin-top: 15px;
    }
    .landing img{
        height: 250px;
        margin-top: 40px;
    }
    .landing h1 {
        color: #FFFFFF;
        transform: translateY(-50px);
    }
}
@media screen and (max-width:650px){
    .navbar{
        padding-left: 10px!important;
        padding-right: 10px!important;
    }
    .hero h1{
        margin-top: -160px;
        font-size: 50px;
    }
    .swiper-container {
        height: 300px;
    }
}
@media screen and (max-width:425px){
    .navbar{
        padding-left: 10px!important;
        padding-right: 10px!important;
        height: 100px;
    }
    .hero h1{
        margin-top: -125px;
        font-size: 35px;
    }
    .navbar-nav li{
        margin-right: 0px;
    }
    .logo{
        height: 40px;
        width: 50px;
    }
    .navbar-light .navbar-brand{
        margin-right: 0%;
        font-size: 20px;
    }
    .navbar-toggler{
        padding: 2px;
    }
    .navbar-nav li a{
        font-size: 20px;
    }
    .landing img{
        height: 180px;
        margin-top: 40px;
    }
    .landing h1 {
        color: #FFFFFF;
        transform: translateY(-50px);
    }
    .navbar-brand .base-font{
        word-spacing: -5px;
        letter-spacing: -4px;
    }
    .swiper-container {
        height: 200px;
    }
    .residential-head,.commercial-head,.retail-head,.corporate-head,.intitutional-head,.health-head,.industry-head{
        font-size: 30px;
    }
}
@media screen and (max-width:320px){
    .hero h1{
        margin-top: -95px;
        font-size: 30px;
    }
}
