.contact-div{
    background: #72C1AE;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    border: none;
    text-align: center;
}
.contact-div .row{
    margin-left: 0%;
    margin-right: 0%;
}
.contact-div h4{
    text-align: center;
    margin: 0px;
    width: 100%;
    padding: 40px;
    font-weight: 700;
    font-size: 40px;
}
address{
    width: 80%;
    text-align: left;
    margin: auto;
    margin-top: 80px;
    margin-bottom: 30px;
}
form{
    width: 80%;
}
form label{
    text-transform: uppercase;
    font-weight: bold;
    display: block;
    text-align: left;
    margin-bottom: 2px;
}
input{
    height: 40px;
}
input, textarea{
    width: 100%;
    border: none;
    outline: none;
    border-radius: 8px;
    padding: 10px;
    margin-bottom: 10px;
}
button{
    border: none;
    outline: none;
    font-weight: bold;
    padding: 15px 30px;
    background: #fff;
    border-radius: 8px;
    margin: 20px 0px;
}
button:focus,button:active{
    outline: none;
}
.social-div{
    width: 30%;
}
.social-div img{
    margin: 20px 0px 40px 0px;
    height: 40px;
    width: 40px;
}
.contact-div .copyright{
    padding: 10px;
    background: #16332C;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 0%;
}

@media screen and (max-width:768px){
    form{
        width: 60%;
    }
    address{
        margin-top: 30px;
        margin-bottom: 30px;
        width: 50%;
    }
}
@media screen and (max-width:425px){
    form{
        width: 80%;
    }
    .social-div{
        width: 60%;
    }
    .contact-div .copyright{
        font-size: 13px;
    }
    address{
        width: 70%;
    }
}